@use '../../00-base/init' as *;

.o-section {
  /**
    * @tokens Section
    * @presenter Color
  */
  --ctek-section-padding-block: 0;
  --ctek-section-padding-inline: var(--ctek-global-space-site);
  --ctek-section-padding: var(--ctek-section-padding-block) var(--ctek-section-padding-inline);
  --ctek-section-margin-item: var(--ctek-global-space-8);

  /**
    * @tokens-end
  */

  padding: var(--ctek-section-padding);

  &.o-section--background-image {
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgb(255 255 255 / 75%);
      content: '';
    }
  }
}

.o-section__wrapper {
  margin-inline: auto;
  position: relative;
  z-index: var(--ctek-global-layer-1);
}

.o-section__content {

  > *:not(:last-of-type) {
    margin-bottom: var(--ctek-section-margin-item);
  }
}
